<template>
    <div id="form">
        <div class="sugdel" @click="modal = false; modal2 = false;"></div>

            <label for="inp" class="inp">

                <input type="text" autocomplete="off" v-model="pays" @focus="modal2= true;reset_var()" class="field-input" name="doge"/>

                <span class="label">Pays</span>
                <span class="border"></span>

            </label>

        <nav v-if="paystab && modal2" class="relative z-10">
            <ul v-for="(lepays, x) in paystab" :key="`L-${x}`" class="suges">
                <li v-if="lepays != null" style="cursor:pointer;" @click="replace(lepays);">{{ lepays }}</li>
            </ul>
        </nav>

        <div v-if="pays === 'France'" class="relative z-10">

            <button class="btn first" v-on:click="modal2 = false; (select_form = 'siret/siren') && reset_var()">siret/siren</button>
            <button class="btn first" v-on:click="modal2 = false; (select_form = 'adresse') && reset_var()">adresse</button>

            <div id="error" v-if="select_form === 'adresse'">

                <label for="inp" class="inp">

                    <input type="text" v-model="raison_sociale" placeholder=" " @click="modal = false; modal2 = false;"/>
                    <span class="label">Raison Sociale</span>
                    <span class="border"></span>

                </label>
                <div></div>
                <label for="inp" class="inp">

                    <input type="text" placeholder=" " autocomplete="off" v-model="adresse1" @focus="modal = true;modal2 = false;">
                    <span class="label">Adresse</span>
                    <span class="border"></span>

                </label>
                <div></div>
                <div v-if="adresse1 && modal">
                        <ul class="suges" v-for="(feature, x) in filteredStates.features" :key="`A-${x}`">
                            <li @click="setState(feature.properties.name, feature.properties.city, feature.properties.postcode, feature.geometry.coordinates[0], feature.geometry.coordinates[1], feature.properties.label, null)">
                                {{feature.properties.label}}
                            </li>
                        </ul>
                </div>

                <label for="inp" class="inp">

                    <input type="text" v-model="adresse2" placeholder=" " @click="modal = false;modal2 = false;"/>
                    <span class="label">Adresse 2</span>
                    <span class="border"></span>

                </label>
                <div></div>
                <label for="inp" class="inp">

                    <input type="text" v-model="adresse3" placeholder=" " @click="modal = false;modal2 = false;"/>
                    <span class="label">Adresse 3</span>
                    <span class="border"></span>

                </label>
                <div></div>
                <label for="inp" class="inp">

                    <input type="text" v-model="postcode" placeholder=" " @click="modal = false;modal2 = false;"/>
                    <span class="label">Code postal</span>
                    <span class="border"></span>
                    
                </label>
                <div></div>
                <label for="inp" class="inp">

                    <input type="text" v-model="ville" placeholder=" " @click="modal = false;modal2 = false;"/>
                    <span class="label">Ville</span>
                    <span class="border"></span>
                    
                </label>
                <div></div>

                <button class="btn first" @click="modal2 = false;reset_error();add_popup();">valider</button>
            </div>

            <div v-if="select_form === 'siret/siren'">

                <p class="sar">Veuillez entrer votre numéro de Siret ou Siren</p>

                <label for="inp" class="inp">

                    <input type="text" autocomplete="off" v-model="siret" placeholder=" " @focus="resetSiren();resetSiret()" @click="modal=true;modal2 = false;"/>
                    <span class="label">Siret</span>
                    <span class="border"></span>

                </label>
                <div></div>
                <div v-if="(siret && !siren) && modal">
                    <ul class="suges" v-if="filteredStates.etablissement != null">
                        <li @click="setState(filteredStates.etablissement.geo_l4, filteredStates.etablissement.libelle_commune, filteredStates.etablissement.code_postal, filteredStates.etablissement.longitude, filteredStates.etablissement.latitude, filteredStates.etablissement.geo_adresse, filteredStates.etablissement.unite_legale.denomination)">
                            {{filteredStates.etablissement.geo_adresse}}
                        </li>
                    </ul>
                </div>
                <label for="inp" class="inp">

                    <input type="text" autocomplete="off" v-model="siren" placeholder=" " @focus="resetSiret();resetSiren()" @click="modal = true ;modal2 = false;"/>
                    <span class="label">Siren</span>
                    <span class="border"></span>

                </label>
                <div></div>
                <div v-if="(siren && !siret) && modal">
                    <ul class="suges" v-if="typeof filteredStates != 'undefined' && filteredStates != null && typeof filteredStates.unite_legale != 'undefined'">
                        <li @click="setState(filteredStates.unite_legale.etablissements[0].geo_l4, filteredStates.unite_legale.etablissements[0].libelle_commune, filteredStates.unite_legale.etablissements[0].code_postal, filteredStates.unite_legale.etablissements[0].longitude, filteredStates.unite_legale.etablissements[0].latitude, filteredStates.unite_legale.etablissements[0].geo_adresse, filteredStates.unite_legale.denomination)">
                            {{filteredStates.unite_legale.etablissements[0].geo_adresse}}
                        </li>
                    </ul>
                </div>

                <button class="btn first" @click="modal2 = false;reset_error();add_popup();">valider</button>

            </div>

        </div>

        <div v-if="pays != 'France'">

            <div v-for="(continent, index) in continents" :key="index">

                <div v-for="(cont, index) in continent" :key="`B-${index}`">

                    <div id="error2" v-if="pays === cont.translations.fr">

                        <label for="inp" class="inp">

                            <input type="text" v-model="raison_sociale" placeholder=" " @click="modal = false; modal2 = false;"/>
                            <span class="label">Raison Sociale</span>
                            <span class="border"></span>

                        </label>
                        <div></div>
                        <label for="inp" class="inp">

                            <input type="text" v-model="adresse1" placeholder=" " autocomplete="off"  @click="modal2 = false;"/>
                            <span class="label">Adresse</span>
                            <span class="border"></span>

                        </label>
                        <div></div>

                        <label for="inp" class="inp">

                            <input type="text" v-model="adresse2" placeholder=" "  @click="modal2 = false;"/>
                            <span class="label">Adresse 2</span>
                            <span class="border"></span>

                        </label>
                        <div></div>

                        <label for="inp" class="inp">

                            <input type="text" v-model="adresse3" placeholder=" "  @click="modal2 = false;"/>
                            <span class="label">Adresse 3</span>
                            <span class="border"></span>

                        </label>
                        <div></div>

                        <label for="inp" class="inp">

                            <input type="text" v-model="postcode" placeholder=" "  @click="modal2 = false;"/>
                            <span class="label">Code postal</span>
                            <span class="border"></span>
                            
                        </label>
                        <div></div>

                        <label for="inp" class="inp">

                            <input type="text" v-model="ville" placeholder=" "  @click="modal2 = false;"/>
                            <span class="label">Ville</span>
                            <span class="border"></span>
                            
                        </label>
                        <div></div>

                        <button class="btn first relative z-10" @click="modal2 = false;reset_error();add_popup();">valider</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>


<script>

    import axios from 'axios';
    import L from 'leaflet';

    export default {
        data() {

            return {
            api_adresse: "https://api-adresse.data.gouv.fr/search/",
            api_country: "https://restcountries.eu/rest/v2/region/",
            api_entreprise: "https://entreprise.data.gouv.fr/api/sirene/v3/",
            option_siret: "etablissements/",
            option_siren: "unites_legales/",
            option_adresse: "?q=",
            option_limit: "&limit=10",
            option_ville: "&type=street",
            option_postcode: '&postcode=',
            siret: '',
            siren: '',
            label: '',
            raison_sociale: '',
            adresse1: '',
            adresse2: '',
            adresse3: '',
            ville: '',
            postcode: '',
            lat: '',
            lon: '',
            http: '',
            select_form: '',

            complement_adresse: '',
            popup: document.createElement("ul"),
            info_popup: document.createElement("li"),
            map: document.createElement("div"),
            mymap: document.createElement("div"),
            close: document.createElement("button"),
            erreur_sociale: document.createElement("p"),
            erreur_complete: document.createElement("p"),
            erreur_etrangere: document.createElement("p"),

            autocomplete: false,
            check_input: false,
            searching: false,
            modal: false,
            modal2: false,

            pays: "",
            paysta: [],
            paystab: [],
            compteur: 0,
            states: [],
            filteredStates: [],

            continents:[],
            Ncontinents:[
                'europe',
                'asia',
                'africa',
                'americas',
                'oceania'
            ],
            xcontinent: 0,
            }
        },

        created() {
            if (this.xcontinent == 0) {
                while (this.xcontinent < this.Ncontinents.length) {
                    axios.get(this.api_country + this.Ncontinents[this.xcontinent])
                    .then(response => {
                        this.continents.push(response.data);

                        let cpt = 0;
                        if (this.compteur == this.continents.length - 1) {
                            while (cpt < this.continents[this.compteur].length) {
                                if (this.continents[this.compteur][cpt].translations.fr != 'France') {
                                    this.paysta.push(this.continents[this.compteur][cpt].translations.fr);
                                }
                                if (this.continents[this.compteur][cpt].translations.fr == 'France') {
                                    this.paysta.unshift(this.continents[this.compteur][cpt].translations.fr);
                                }
                                cpt++;
                            }
                            this.compteur = this.compteur + 1;
                        }
                        this.paystab = this.paysta;
                    })
                    this.xcontinent = this.xcontinent + 1;
                }
            }
        },

        updated() {

            if(this.adresse1){
                this.http = this.api_adresse + this.option_adresse + this.adresse1

                if(this.ville) {
                    this.http = this.http + "+" + this.ville + this.option_ville
                }
                if (this.postcode) {
                    this.http = this.http + this.option_postcode + this.postcode
                }
                axios.get(this.http)
                .then(response => {
                    this.filterStates(response.data);
                })
            }

            if(this.siret && !this.siren) {
                if (this.siret.length == 14 && this.siret != "") {
                    this.http = this.api_entreprise + this.option_siret + this.siret
                    axios.get(this.http)
                    .then(response => {
                        this.filterStates(response.data);
                    })
                }
            }

            if(this.siren && !this.siret) {
                if (this.siren.length == 9 && this.siren != "") {
                    this.http = this.api_entreprise + this.option_siren + this.siren
                    axios.get(this.http)
                    .then(response => {
                        this.filterStates(response.data);
                    })
                }
            }
        },

        methods: {

            reset_error() {
                if (this.erreur_complete.innerHTML) {
                    this.erreur_complete.innerHTML = '';
                    document.getElementById('error').removeChild(this.erreur_complete);
                }
                if (this.erreur_sociale.innerHTML) {
                    this.erreur_sociale.innerHTML = '';
                    document.getElementById('error').removeChild(this.erreur_sociale);
                }
                if (this.raison_sociale && this.adresse1 && this.postcode && this.ville) {
                    if (this.erreur_etrangere.innerHTML) {
                        this.erreur_etrangere.innerHTML = '';
                        document.getElementById('error').removeChild(this.erreur_etrangere);
                    }
                    this.check_input = true;
                }
            },

            reset_var() {
                this.siret = '';
                this.siren = '';
                this.raison_sociale = '';
                this.adresse1 = '';
                this.adresse2 = '';
                this.adresse3 = '';
                this.ville = '';
                this.postcode = '';
                this.lat = '';
                this.lon = '';
                this.http = '';
                this.autocomplete = false;
                this.reset_error();
            },

            filterStates(states) {
                this.filteredStates = states;
            },

            setState(adresse1, ville, postcode, lon, lat, label, sociale) {
                this.adresse1 = adresse1;
                this.ville = ville;
                this.postcode = postcode;
                this.modal = false; 
                this.lat = lat;
                this.lon = lon;
                this.label = label;
                this.autocomplete = true;
                if (this.select_form === 'siret/siren') {
                    this.raison_sociale = sociale;
                }
            },

            add_popup() {
                if (this.pays == 'France' && this.select_form == 'adresse') {
                    if (this.autocomplete && this.raison_sociale) {
                        this.check_input = true;
                    }
                    else {
                        this.check_input = false;
                        
                        if (this.raison_sociale == false) {
                            this.erreur_sociale.innerHTML = "Vous devez renseigner le champ Raison Sociale";
                            this.erreur_sociale.className = "sar2";
                            document.getElementById("error").appendChild(this.erreur_sociale);
                        }
                        if (this.autocomplete == false) {
                            this.erreur_complete.innerHTML = "Vous devez sélectionner une suggestion dans le champ adresse";
                            this.erreur_complete.className = "sar2";
                            document.getElementById("error").appendChild(this.erreur_complete);
                        }
                    }
                }
                if (this.pays == 'France' && this.select_form == 'siret/siren') {
                    if (this.autocomplete) {
                        this.check_input = true;
                    }
                    else {
                        this.check_input = false;
                    }
                }
                if (this.pays != 'France') {
                    if (this.raison_sociale && this.adresse1 && this.postcode && this.ville) {
                        this.check_input = true;
                    }
                    else {
                        this.check_input = false;
                        this.erreur_etrangere.innerHTML = "Veuillez renseigner les champs Raison Sociale, Adresse, Code Postale et Ville.";
                        this.erreur_etrangere.className = "sar2";
                        document.getElementById("error2").appendChild(this.erreur_etrangere);
                    }
                }

                if (this.check_input) {
                    this.init_popup();
                }
            },

            init_popup() {
                this.map = document.createElement('div');
                this.mymap = document.createElement('div');

                this.mymap.setAttribute("id", "mymap");
                document.getElementById("form").appendChild(this.mymap);

                if (this.pays != 'France' && this.adresse1) {
                    this.complement_adresse = "<li class='lipop'>" + this.adresse1 +"</li>";
                }
                if (this.adresse2) {
                    this.complement_adresse += "<li class='lipop'>" + this.adresse2 +"</li>";
                }
                if (this.adresse3) {
                    this.complement_adresse += "<li class='lipop'>" + this.adresse3 +"</li>";
                }
                if (this.pays != 'France' && this.postcode) {
                    this.complement_adresse += "<li class='lipop'>" + this.postcode +"</li>";
                }
                if (this.pays != 'France' && this.ville) {
                    this.complement_adresse += "<li class='lipop'>" + this.ville +"</li>";
                }

                this.popup.innerHTML = "<li class='lipop'>" + this.raison_sociale + "</li><li class='lipop'>" + this.label + "</li>" + this.complement_adresse + "<li class='lipop'>" + this.pays + "</li>";
                document.getElementById('mymap').appendChild(this.popup);
                this.popup.setAttribute("id", "popup");

                if (this.pays == 'France') {
                    this.map.setAttribute("id", "map");
                    document.getElementById('mymap').appendChild(this.map);
                    
                    let mymap = L.map('map').setView([this.lat, this.lon], 18);
                    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                        attribution: '&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
                    }).addTo(mymap);
                    L.marker([this.lat, this.lon]).addTo(mymap)
                    .bindPopup(this.adresse1)
                    .openPopup();
                }

                this.close.innerHTML = "Fermer";
                this.close.className = "btn first";
                this.close.setAttribute("onclick", "document.getElementById('form').removeChild(document.getElementById('mymap'))");
                document.getElementById('mymap').appendChild(this.close);
            },

            resetSiret() {
                this.siret = '';
                this.autocomplete = false;
            },
            resetSiren() {
                this.siren = '';
                this.autocomplete = false;
            },
            filterpaysta() {
                let x = 0;
                this.paystab = [];
                while (this.paysta.length > x) {
                    if (this.paysta[x] !== null && this.paysta[x] !== "") {
                        if (this.paysta[x].toLowerCase().startsWith(this.pays.toLowerCase())) {
                            this.paystab.push(this.paysta[x]);
                        }
                    }
                    x++;
                }
            },
            replace(pays) {
                this.pays = pays;
                this.modal2 = false;
            }
        },

        watch: {
            pays() {
                this.filterpaysta();
            }
        }

    }

</script>

<style>

@import"../css/fichier.css";

</style>